import React from 'react'
import "./styles.css"
import 'bootstrap/dist/css/bootstrap.min.css';

export default function Desc() {
  return (
    <div className="description" id='firm'>
        <p>
          <hr />
          Pracownia zegarmistrzowska Henryka Trzoska powstaa w 1963 roku w Warszawie i niezmiennie od pocztku istnienia zwizana jest z Mokotowem. Obecnie znajduje si przy ulicy Puawskiej 96, nieopodal zbiegu z ulic Odyca. Dzi trudno sobie wyobrazi, e jej tam nie ma - zrosa si trwale z tkank poudniowej dzielnicy Warszawy. Charakterystyczny zegar, zdobicy front kamienicy nie tylko wskae miejsce, w ktrym czas jest najwaniejszy, ale pozwala niepiesznym przechodniom upewni si, e "Tempus Fugit". Jednak przy Puawskiej 96 czas jakby si zatrzyma. Tutaj bowiem stare tradycyjne rzemioso znakomicie broni si przed nowymi technologiami, jednoczenie - co zrozumiae - ustpujc im nieco miejsca. Mokotowska pracownia, to dwa pokolenia zegarmistrzw, ktrzy dbaj o to co byo dawniej i o to, co jest aktualne i modne dzi. Obecnie firma dziaa pod nazw Trzosek Henryk i Syn. Od lat 90-tych panowie Henryk i Maciej dbaj o zegary odmierzajce czas w Paacu Prezydenckim, Belwederze i Urzdzie Rady Ministrw. Aby duszpasterze mogli spenia swoj misj we waciwym czasie, firma Trzosek Henryk i Syn doglda mechanizmw w Archidiecezji Warszawskiej. Pod opiek konserwatorsk znalazy si take zegary uytecznoci publicznej: koci w Lipnie, koci w Puawach, a take zegar w jednym z warszawskich urzdw skarbowych. W portfolio Macieja znajdziemy dowiadczenie zdobyte w La Boutique Swiss, Hermitage Boutique, Hublot, Swatch Group, Wrzeciono Czasu. Dla zainteresowanych nazwy te nie stanowi tajemnicy. Do pracowni panw Henryka i Macieja zawsze warto zajrze. Jest to miejsce wyjtkowe na usugowej mapie wspczesnej Warszaw. Tradycja czy si tu z nowoczesnoci, a niepowtarzalny klimat i fachowa obsuga sprawiaj, e na Puawsk 96 chce si po prostu wraca.
          <hr />
        </p>
    </div>
  )
}
